<template>
    <div id="login">
        <div class="swiper-container">
        　　<div class="swiper-wrapper">
        　　　　<div class="swiper-slide swiper-no-swiping" v-for="(item,k) in swiperitem" :key="k"><img :src="item.src"></div>
        　　</div>
        </div>
        <div class="login">
            <div class="login_left">
                <div class="systemName">玉带职业技术学校教学诊断与改进系统</div>
                <div class="foot">
                    <p>Copyright © 2018-2019 上海玉带信息技术有限公司 版权所有</p>
                    <div class="call">
                        <span @click="toBackStage" style="font-size:13px">技术支持平台：</span>
                        <!-- <span style="font-size:10px">http://www.xqit.com 在线支持</span> -->
                        <a target="_blank" href="http://wpa.qq.com/msgrd?v=3&uin=2310401&site=qq&menu=yes"><img border="0" src="http://wpa.qq.com/pa?p=2:2310401:51" alt="DID系统技术与业务支持" title="DID系统技术与业务支持"/></a></div>
                </div>
            </div>
            <div class="login_right">
                <div class="logo"><img :src="logo" alt=""></div>
                <el-form :model="userInfo" ref="userInfo" class="loginForm">
                    <el-form-item prop="user">
                        <el-input type="text" v-model="userInfo.user" autocomplete="off" placeholder="请输入账号"></el-input>
                    </el-form-item>
                    <el-form-item prop="pass">
                        <el-input type="password" v-model="userInfo.pass" autocomplete="off" suffix-icon="iconfont icon-suo" placeholder="请输入密码"></el-input>
                    </el-form-item>
                    <el-form-item prop="system">
                        <el-select v-model="userInfo.system" placeholder="请选择系统">
                            <el-option label="诊改数据深度挖掘平台" value="1"></el-option>
                            <el-option label="诊改双链工作管理平台" value="2"></el-option>
                            <!-- <el-option label="诊改后台管理工作平台" value="3"></el-option> -->
                            <el-option label="诊改课堂数据统计平台" value="4"></el-option>
                            <el-option label="诊改校本数据中心" value="5"></el-option>
                            <el-option label="诊改大屏全景展示平台" value="6"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button class="login_btn" type="primary" @click="login()">登录</el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button class="forget_btn" @click="forget()">忘记密码</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
import Swiper from 'swiper';
import 'swiper/dist/css/swiper.min.css';
export default {
    name: 'login',
    data(){
        return{
            swiperitem:[
                {src: 'http://cdn.xqit.net/zg/img/bg1.jpg'},
                {src: 'http://cdn.xqit.net/zg/img/bg2.jpg'},
                {src: 'http://cdn.xqit.net/zg/img/bg3.jpg'},
                {src: 'http://cdn.xqit.net/zg/img/bg4.jpg'},
                {src: 'http://cdn.xqit.net/zg/img/bg5.jpg'},
                {src: 'http://cdn.xqit.net/zg/img/bg6.jpg'},
                {src: 'http://cdn.xqit.net/zg/img/bg7.jpg'},
                {src: 'http://cdn.xqit.net/zg/img/bg8.jpg'},
            ],
            login_left: 'http://cdn.xqit.net/zg/img/did.png',
            logo: 'http://cdn.xqit.net/zg/img/login_logo.png',
            userInfo: {
                user: '',
                pass: '',
                system: '1',
            }
        }
    },
    methods: {
        toBackStage(){
            window.location.href = window.location.href = 'http://zg.demo.xqit.net/ydjxzg/houtaiguanli/#/'   // 线上
            // window.location.href = 'http://zg.demo.xqit.net:83/#/'+'?name='+res.login.name+'&dj='+res.login.dj+'&gender='+res.login.gender+'&user_id='+res.login.user_id  //开发
        },
        login(){
            var _this = this
            if(this.userInfo.user == ''){
                this.$message.warning('账号不能为空！')
            }else if(this.userInfo.pass == ''){
                this.$message.warning('密码不能为空！')
            }else{      //将账号密码发送给后台进行登陆判断
                // console.log(_this.userInfo.user,_this.userInfo.pass)
                $.ajax({
                    type: "post",
                    url: _this.baseUrl+"/ydjxzg/log/login.action",
                    //url: "/ydjxzg/log/login.action",
                    data: {username:_this.userInfo.user,password:_this.userInfo.pass},
                    dataType: "json",
                    success: function (res) {
                        console.log(res)
                        sessionStorage.setItem("user",res.login.name);
                        sessionStorage.setItem("userdj",res.login.dj);
                        sessionStorage.setItem("user_id",res.login.user_id);
                        sessionStorage.setItem("usergender",res.login.gender);
                        sessionStorage.setItem("permis", JSON.stringify(res.permis));
                        console.log(sessionStorage.getItem("permis"));
                        // console.log(res.permis)
                        if(res.mess){
                            if(_this.userInfo.system == '1'){          //判断要进入的系统类型
                                console.log('数据挖掘')
                                // sessionStorage.setItem("user",res.login.name);
                                // console.log(res.login)
                                // sessionStorage.setItem("userdj",res.login.dj);
                                // sessionStorage.setItem("user_id",res.login.user_id);
                                // sessionStorage.setItem("usergender",res.login.gender);
                                sessionStorage.setItem("login","isLogin");
                                _this.$router.push({path: '/hxsj'}) //使用params将登陆状态和用户名传到其它页面后用sessionStorage保存起来在需要的地方调用
                                _this.$message.success('登录成功')
                            }else if(_this.userInfo.system == '2'){
                                console.log('双链管理')
                                window.location.href = 'http://zg.demo.xqit.net:81/#/zgmb'+'?name='+res.login.name+'&dj='+res.login.dj+'&gender='+res.login.gender+'&user_id='+res.login.user_id  //开发
                                // window.location.href = '/ydjxzg/index.html#/zgmb'+'?name='+res.login.name+'&dj='+res.login.dj+'&gender='+res.login.gender+'&user_id='+res.login.user_id  //线上
                                // window.location.href = '/ydjxzg/shuanglianguanli/index.html#/zgmb'+'?name='+res.login.name+'&dj='+res.login.dj+'&gender='+res.login.gender+'&user_id='+res.login.user_id  //线上
                                //window.location.href = process.env.BASE_URL +'?name='+res.login.name+'&dj='+res.login.dj+'&gender='+res.login.gender+'&user_id='+res.login.user_id  //开发
                            }
                            else if(_this.userInfo.system == '3'){
                                console.log('后台管理')
                                // window.location.href = 'http://zg.demo.xqit.net:83/#/'+'?name='+res.login.name+'&dj='+res.login.dj+'&gender='+res.login.gender+'&user_id='+res.login.user_id  //开发
                                // window.location.href = '/ydjxzg/index.html#/HelloWorld'+'?name='+res.login.name+'&dj='+res.login.dj+'&gender='+res.login.gender+'&user_id='+res.login.user_id  //线上
                                // window.location.href = '/ydjxzg/houtaiguanli/index.html#/'+'?name='+res.login.name+'&dj='+res.login.dj+'&gender='+res.login.gender+'&user_id='+res.login.user_id  //线上
                                //window.location.href = process.env.BASE_URL +'?name='+res.login.name+'&dj='+res.login.dj+'&gender='+res.login.gender+'&user_id='+res.login.user_id  //开发
                            }
                        }else{
                            _this.$message.error('用户名或密码错误')
                        }
                    },
                });
            }
        },
        forget(){       //忘记密码
            this.$router.push({
                path: '/wangji',
                name: 'wangji'
            });
        }
    },
    mounted() {
        console.log(process.env.BASE_URL)
        var _this = this
　　　　 var mySwiper = new Swiper(".swiper-container",{
　　　　　　 //direction:"horizontal",     //横向滑动
            effect : 'fade',
            noSwiping: true,    //必须配合swiper-no-swiping,哪个滑块不需要滑动，就加这个类
　　　　 　　loop:true,         //形成环路,可以从最后一张图跳转到第一张图 
　　　　 　　autoplay:{
                delay: 3000,    //3秒切换一次
            },
　　　　 });
    },
}
</script>

<style>
    #login{
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: relative;
    }
    #login .swiper-container{
        width: 100%;
        height: 100%;
        display: flex;
    }
    #login .swiper-container{
        width: 100%;
        height: 100%;
    }
    #login .swiper-slide{
        width: 100%;
        height: 100%;
        display: flex;
    }
    #login .swiper-slide img{
        width: 100%;
        height: 100%;
    }
    #login .login{
        width: 800px;
        height: 432px;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        z-index: 100;;
    }
    .login_left{
        width: 448px;
        height: 432px;
        float: left;
        background: url('http://cdn.xqit.net/zg/img/did.png') center no-repeat;
        background-size: 100%;
        position: relative;
    }
    #login .systemName{
        display: block;
        width: 100%;
        font-size: 16px;
        color: #ffffff;
        letter-spacing: 8px;
        text-align: center;
        text-shadow: 0px 3px 3px #75bae7;
        position: absolute;
        top: 96px;
    }
    #login .call{
        display: flex;
        justify-content: center;
    }
    #login .call a{
        color: #ffffff;
        margin-right: 5px;
    }
    #login .foot{
        width: 100%;
        font-size: 12px;
        color: #ffffff;
        text-align: center;
        line-height: 20px;
        position: absolute;
        bottom: 8px;
    }
    #login .login_right{
        width: 352px;
        height: 432px;
        background: #ffffff;
        float: left;
        position: relative;
    }
    #login .logo{
        width: 124px;
        height: 124px;
        overflow: hidden;
        position: absolute;
        left: 115px;
        top: 36px;
    }
    #login .logo img{
        width: 100%;
        height: 100%;
    }
    #login .loginForm{
        width: 200px;
        height: auto;
        position: absolute;
        bottom: 16px;
        left: 80px;
    }
    #login .el-form-item{
        margin-bottom: 10px;
    }
    #login .el-form-item__content,.el-select{
        width: 100%;
        height: 34px;
        line-height: 34px;
    }
    #login .el-input__inner{
        height: 34px;
        background: #eeeeee!important;
        color: #666666;
        box-shadow: 0px 3px 3px #c8c8c8 inset
    }
    #login .login_btn{
        width: 100%;
        height: 30px;
        line-height: 30px;
        padding: 0;
        background: #32a8ee;
    }
    #login .forget_btn{
        width: 100%;
        height: 30px;
        line-height: 30px;
        padding: 0;
        border-color: #32a8ee;
        color: #32a8ee;
    }
    #login .el-input__suffix{
        display: inline-block;
        width: 25px;
        height: 34px;
        line-height: 34px;
    }
    #login .el-input__suffix .el-input__suffix-inner i{
        display: inline-block;
        width: 25px;
        height: 34px;
        line-height: 34px;
    }
</style>



